interface TowerItem {
  cost: number;
  info: string;
}

interface TowerData {
  BULLET: TowerItem;
  LASER: TowerItem;
  SLOW: TowerItem;
  FIRE: TowerItem;
  BLOCK: TowerItem;
  [index: string]: TowerItem;
}

